import {
	getCode,
	getPhoneNumber,
	Login
} from '../../api/login/index.js'
import {
	goPage
} from '../../util/util.js'
export default {
	data() {
		return {
			params: {},
			checkDesFlag: true, // 是否选中协议
			loginFlag: 0, // 0:微信授权登录，1：手机号登录
			jumpSource:0,//1切換账号来源跳转到个人中心
			phone: {
				phoneNumber: '',
				code: '',
			},
			timer: null,
			show: true,
			count: 0,
			sssId: ''
		}
	},
	onLoad(opts) {
		uni.hideShareMenu()
		this.params = opts.params ? JSON.parse(opts.params) : {}
		this.sssId = opts.sss_id || ''
		this.loginFlag = opts.loginFlag || 0
		this.jumpSource = opts.jumpSource || 0
	},
	watch:{
		loginFlag(v){
			if(v == 1){
				uni.setNavigationBarTitle({
					title:'手机验证码登录'
				})
			}
		}
	},
	methods: {
		showLoading(){
			uni.showLoading({
				title:'处理中...',
				mask:true
			})
		},
		// 获取手机号
		getPhoneNumber(e) {
			
			if (e.detail.errMsg == 'getPhoneNumber:ok') {
				uni.showToast({
					title: '获取手机号成功',
					mask: true,
					duration: 2000,
					icon: 'none'
				});
				// 调用获取手机号接口
				getPhoneNumber({
					data: {
						encrypted_data: e.detail.encryptedData,
						iv: e.detail.iv
					}
				}).then((res) => { 
					if (res.data.code == 0 && res.data.result.flag == 1) {
						uni.setStorageSync('isMobile', 1)
						if (!this.sssId) {
							uni.navigateBack()
						} else {
							goPage(Object.assign(this.params, {
								sss_id: this.sssId
							}), 'login')
						}
					} 
				})


			} else if (e.detail.errMsg == 'getPhoneNumber:fail user deny') {
				uni.showToast({
					title: '你拒绝了授权',
					mask: true,
					duration: 2000,
					icon: 'none'
				});
			} else {
				uni.showToast({
					title: '授权失败',
					mask: true,
					duration: 2000,
					icon: 'none'
				})
			}
		},
		// 切换同意协议
		checkDes() { 
			this.checkDesFlag = !this.checkDesFlag;
		},
		changePhone(e) {
			this.phone.phoneNumber = e.detail.value;
		},
		changeCode(e) {
			this.phone.code = e.detail.value;
		},
		phoneLogin() {
			let that = this;
			uni.login({
				provider: 'weixin',
				success: function (e) {
					Login({
						data: {
							login_name: that.phone.phoneNumber,
							captch_code: that.phone.code,
							weixin_code:e.code
						}
					}).then((res) => {
						if (res.data.code == 0) {
							uni.hideLoading()
							uni.setStorageSync('isMobile', 1)
							uni.setStorageSync('token',res.data.result.token)
							if (!that.sssId && that.jumpSource ==0) {
								uni.navigateBack()
							}else if(that.jumpSource == 1){
								uni.switchTab({
									url:'/pages/user/user'
								})
							}else {
								goPage(Object.assign(that.params, {
									sss_id: that.sssId
								}), 'login')
							}
						}
					})
				}
			});
			
			
		},
		getCode() {
			getCode({
				data: {
					login_name: this.phone.phoneNumber,
					type: '8'
				} 
			}).then((res) => { 
				if (res.data.code == 0) {
					uni.showToast({
						mask:true,
						icon:'none',
						title:'获取成功，请注意查收'
					})
					const TIME_COUNT = 60;
					if (!this.timer) {
						this.count = TIME_COUNT;
						this.show = false;
						this.timer = setInterval(() => {
							if (this.count > 0 && this.count <= TIME_COUNT) {
								this.count--;
							} else {
								this.show = true;
								clearInterval(this.timer);
								this.timer = null;
							}
						}, 1000)
					}
				}
			}) 
		},
		// 跳转协议页面
		openWeb(){
			let src = 'https://static.car-me.com/cms/content/bldmexieyi.html';
			uni.navigateTo({
				url:`/pages/home/web-view/web-view?url=${src}&title=用户协议`
			})
		}
	}
}
